@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:border-box}h1,h2{margin:0}ul{list-style:none;padding:0;margin:0}.search-form{margin:30px;text-align:center}.search-form .search-bar{padding:5px;width:20%}.search-form .search-btn{padding:5px 15px;margin-left:10px;cursor:pointer;border:1px solid #afafaf;border-radius:5px;background-color:white;color:#5b8f24;font-weight:bold;outline:none}.search-form .search-btn:hover{color:#82c836}.recipes{display:flex;justify-content:space-around;flex-wrap:wrap}

header{background-color:#82c836;padding:25px 0}header h1{font-family:'Major Mono Display', monospace;text-align:center;color:white}

footer{background-color:#82c836;padding:15px 0}footer p{font-family:'Montserrat', sans-serif;font-size:.9em;text-align:center;color:white}

.recipe-card{border-radius:10px;box-shadow:0px 5px 15px #626262;margin:20px;padding:30px;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.recipe-card h2,.recipe-card h4,.recipe-card p{font-family:'Montserrat', sans-serif}.recipe-card h4{margin-bottom:5px}.recipe-card li{font-size:.8em}

